.Cards {
  background-color: #F1F1F1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
}

@media (min-width: 800px) {
  .Card-item {
    width: 33.3333%;
  }
}
@media (min-width: 1400px) {
  .Card-item {
    width: 25%;
  }
}
@media (min-width: 2000px) {
  .Card-item {
    width: 20%;
  }
}

.Card {
  margin: 1rem;
  background-color: white;
  border-radius: 0.25rem;
  -webkit-box-shadow: 0 20px 40px -14px rgba(0,0,0,0.25);
          box-shadow: 0 20px 40px -14px rgba(0,0,0,0.25);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  overflow: hidden;
  -ms-flex-align: center;
      align-items: center;
}

.Card-header {
  width: 90%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 0.2rem;
  padding: 0 5px 0 5px;
}

.Card-clipboard-img {
  width: 16px;
  height: 16px;
  padding: 0px 3px 0px 6px;
	cursor: pointer;
}

.Card-clipboard-img:hover {
  opacity:.5;
}

.Card-header-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.Card-header-icon {
  height: 1.4rem;
  width: 1.4rem;
  border-radius: 180px;
  margin: 0px;
}

.Card-header-title {
  font-size: 1rem;
  margin: 0.8rem 0rem 0.8rem 0.4rem;
  font-weight: normal;
}

.Card-header-subtitle {
  font-size: 0.9rem;
  font-weight: 300;
  margin: 0px;
}

.Card-image {
  height: auto;
  max-width: 100%;
  vertical-align: middle;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  /*filter: contrast(85%);*/
  overflow: hidden;
  position: relative;
  -webkit-transition: -webkit-filter 0.5s cubic-bezier(.43,.41,.22,.91);
  transition: -webkit-filter 0.5s cubic-bezier(.43,.41,.22,.91);
  -o-transition: filter 0.5s cubic-bezier(.43,.41,.22,.91);
  transition: filter 0.5s cubic-bezier(.43,.41,.22,.91);
  transition: filter 0.5s cubic-bezier(.43,.41,.22,.91), -webkit-filter 0.5s cubic-bezier(.43,.41,.22,.91);;
}

.Card-content {
  display: -ms-flexbox;
  display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  width: 90%;
}


.Card-content-title {
  margin: 0.5rem 0 0 0;
  font-weight: bold;
  font-size: 1rem;
}

.Card-content-last-activity {
  font-size: 0.8rem;
  font-weight: 300;
  margin: 0 0 0.5rem 0;
}

.Card-content-facts {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  border-top: 1px solid #DFDFDF;
  border-bottom: 1px solid #DFDFDF;
  //margin: 1rem 0 0.5rem 0;
}

.Card-content-fact {
  font-size: 0.8rem;
  font-weight: 100;
  margin: 0.25rem;
}

.Card-content-description {
  text-align: left;
  font-size: 0.85rem;
  margin: 0.5rem;
  margin-bottom: 0;
}

.Card-content-download {
  font-size: 1rem;
  color: #666666;
  background-color: #e5f1fb;
  border-radius: 5px;
  border: 0px;
  width: 100%;
  height: 35px;
  margin: 0.5rem 0 0.5rem 0;
	cursor: pointer;
}

.Card-content-download:hover {
	background-color: #d4e0ea;
}



